import { injectGlobal } from "styled-components";
import theme from "./Document.theme.js";

/**************************************************
THIS LOADS INLINE IN HEAD, BLOCKING ALL HTML - SO BE VERY BRIEF - PUT EVERYTHING POSSIBLE INTO /src/pages/client.css
**************************************************/

injectGlobal`

	/**************************************************
		reset
	**************************************************/
	html, body, #app, #pages {
		height:100%;
		width:100%;
	}
	body {
		margin: 0;
		padding: 0;
        color:navy;      
        background:lightblue;
	}


	/**************************************************
		responsive text
	**************************************************/
	html {
		font-size:16.5px;
	}
	@media (max-width:950px), (max-height:750px) {
		html {
			font-size:15.5px;
		}
	}
	@media (max-width:750px), (max-height:650px) {
		html {
			font-size:14.5px;
		}
	}
	@media (max-width:380px), (max-height:380px) {
		html {
			font-size:13.5px;
		}
	}
	@media (max-width:330px), (max-height:330px) {
		html {
			font-size:12.5px;
		}
	}
	h1,h2,h3,h4,h5,h6,p {
		margin:0;
		padding:0;
		line-height:1.5rem;
	}
	p {
		margin:0.75rem 0;
		font-size:1rem;
	}
	h1 {
		font-size:1.9rem;
	}
	h2 {
		font-size:1.75rem;
	}
	h3 {
		font-size:1.6rem;
	}
	h4 {
		font-size:1.45rem;
	}
	h5 {
		font-size:1.3rem;
	}
	h6 {
		font-size:1.15rem;
	}
	li {
		padding-bottom:0.5rem;
	}


	/**************************************************
		style
	**************************************************/
	* {
		font-family: ${theme.fontFamily} !important;
		vertical-align:middle; /* hope this doesn't mess anything up - but in my experience, having vertical-align middle was always more useful than not - PS */
	}
	body {
		background: ${theme.colorDark2};
		color: ${theme.colorOnDark3};
	}
	a {
		color:rgb(100, 123, 146);
		text-decoration: none;
	}
`;



